.cat-card{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    height: 250px;
    padding: 10px;
}

.cat-card:hover {
    transform: scale(1.1);
}
  
.cat-img {
    width: 100%;
    height: 200px;
}
  
.cat-title{
    margin-top: 4%;
    text-align: center;
    font-weight: 700;
}